/* ================= ABOUT PAGE ================= */

.about-section {
  display: flex;
  justify-content: center;
  padding-top: 6.5rem;   /* IMPORTANT */
  padding-bottom: 3rem;
}

.about-grid {
  width: 100%;
  max-width: 1100px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}

.about-left h1,
.about-right h1 {
  margin-bottom: 1.5rem;
}

.about-left p {
  line-height: 1.75;
  margin-bottom: 1rem;
}

.education-item {
  margin-bottom: 1.5rem;
}

/* ---------- MOBILE ---------- */
@media (max-width: 900px) {
  .about-grid {
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }
}
